.navbarContainer{
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 10px 10px;
}
.filterProducts{
    font-size: 18px;
    
}
.filter{
    margin: 15px;
    cursor: pointer;
}
.navbarContainer img{
    width: 150px;
    height: 60px;
}
.navPages a{
    text-decoration: none;
    margin: 15px;
    font-size: 20px;
    color: black;
}
.icons{
    font-weight: bold;
    font-size: 22px;
}
.navPages a:hover{
    color: brown;
}
.navPages a:focus{
   color: brown;
}
